<template>
  <div class="toys">
    <h1>TOYS</h1>
    
    <ToyFilter :toys="toys" @filterd="setFilter" />
    <ToyList :toys="toys"></ToyList>
  </div>
</template>
<script>
import ToyFilter from "../components/ToyFilter.vue";
import ToyList from "../components/ToyList.vue";

export default {
  computed: {
    toys() {
      return this.$store.getters.toyToShow;
    }
  },
  methods: {
    setFilter(filterBy) {
      this.$store.commit("setFilter", filterBy);
    }
  },
  created() {},
  components: {
    ToyList,
    ToyFilter
  }
};
</script>