
<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  props: {
    toys: Object
  },
  data() {
    return {
      chartdata: {
        labels: Object.keys(this.toys),
        datasets: [
          {
            label: "Toys per year",
            backgroundColor: "#f87979",
            data: Object.values(this.toys).concat([0, 5])
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false
      }
    };
  },
  computed: {
    // keys() {
    //   return Object.keys(this.toys);
    // },
  },
  //   created() {
  //     this.labels = Object.keys(this.toys);
  //   },
  mounted() {
    this.chartdata.labels = Object.keys(this.toys);
    this.chartdata.datasets.data = Object.values(this.toys).concat([0, 5]);
    this.renderChart(this.chartdata, this.options);
    // Overwriting base render method with actual data.
  },
  watch: {
    toys(to,from) {
        console.log('got',to,from);
        
      this.chartdata.labels = Object.keys(this.toys);
      this.chartdata.datasets.data = Object.values(this.toys).concat([0, 5]);
      this.renderChart(this.chartdata, this.options);
    }
  }
};
</script>