
<script>
import { Doughnut } from "vue-chartjs";

export default {
  extends: Doughnut,
  props: {
    // toys: Object
  },
  data() {
    return {
      chartdata: {
        labels:['a','b','c'],
        datasets: [
          {
            label: "Toys per year",
            data: [10,20,30]
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false
      }
    };
  
  },
  mounted() {
      this.renderChart(this.chartdata, this.options)
    // Overwriting base render method with actual data.
   
  }
};
</script>