<template>
  <v-row align="center" justify="center">
    <SocialModal class="social" :toyId="toyId">
      <v-icon class="tools-btn" @click="action('share')">{{ icons.mdiShareVariant }}</v-icon>
    </SocialModal>
    <v-icon class="tools-btn" @click="action('edit')">{{ icons.mdiPencil }}</v-icon>
    <div class="mx-2"></div>
    <v-icon class="tools-btn" @click="action('delete')">{{ icons.mdiDelete }}</v-icon>
    <div class="mx-2"></div>
  </v-row>
</template>
<script>
import { mdiAccount, mdiPencil, mdiShareVariant, mdiDelete } from "@mdi/js";
import SocialModal from "../components/SocialModal.vue";
export default {
  data() {
    return {
      icons: {
        mdiAccount,
        mdiPencil,
        mdiShareVariant,
        mdiDelete
      },
      toyId: this.$route.params.id,
    };
  },
  methods: {
    action(type) {
      this.$emit("action", type);
    }
  },
  components: {
    SocialModal
  }
};
</script>