<template>
  <div class="toy-details">
    <div class="toy-img-container">
      <img class="toy-img" :src="toy.imgUrl" />
    </div>

    <v-card class="card-toy-details">
      <v-card-subtitle class="pb-0">{{toy.createdAt|relativeTime}}</v-card-subtitle>

      <v-card-text class="text--primary">
        <div>
          <h3>Price:</h3>
          {{toy.price}}
        </div>

        <div>
          <h3>Type:</h3>
          {{toy.type}}
        </div>
      </v-card-text>

      <v-card-actions>
        <v-btn color="orange" text to="/toy" exact>Toys</v-btn>
      </v-card-actions>
      <v-card-actions>
        <ToyTools @action="action" />
      </v-card-actions>
    </v-card>
  </div>
</template>
<script>
import ToyTools from "../components/ToyTools.vue";
export default {
  data() {
    return {
      toy: ""
    };
  },
  methods: {
    action(type) {
      const id = this.$route.params.id;
      if (type === "edit") this.$router.push(`/toy/edit/${id}`);
      console.log(type);
    }
  },
  created() {
    const id = this.$route.params.id;
    if (id) {
      this.$store.dispatch({ type: "toyById", id }).then(toy => {
        this.toy = toy;
      });
    }
  },
  components: {
    ToyTools
  }
};
</script>