import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Toy from '../views/ToyAppPage.vue'
import About from '../views/About.vue'
import ToyDetails from '../views/ToyDetails.vue'
import ToyEdit from '../views/ToyEdit.vue'
import Dashboard from '../views/Dashboard.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/toy',
    name: 'toy',
    component: Toy
  },
  {
    path: '/toy/edit/:id?',
    name: 'edit',
    component: ToyEdit
  },
  {
    path: '/toy/details/:id',
    name: 'details',
    component: ToyDetails
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: Dashboard
  },
  {
    path: '/about',
    name: 'about',
    component: About
  }
]

const router = new VueRouter({
  routes
})

export default router
