<template>
  <v-row align="center">
    <v-row justify="space-around"></v-row>
    <v-form ref="form" v-model="valid">
      <v-text-field v-model="toy.name" :counter="30" :rules="nameRules" label="Name" required></v-text-field>

      <v-text-field
        v-model="toy.price"
        type="number"
        label="Price $"
        min="0"
        :rules="priceRules"
        required
      ></v-text-field>
      <v-text-field
        list="browsers"
        v-model="toy.type"
        label="Type"
        :rules="[v => !!v || 'Item is required']"
        required
      ></v-text-field>
      <datalist id="browsers">
        <option :value="type" v-for="type in types" :key="type"></option>
      </datalist>
      <v-switch class="ma-4" label="file" v-model="isFile"></v-switch>
      <v-text-field
        v-if="!isFile"
        :rules="urlRules"
        v-model="toy.imgUrl"
        label="Image Url"
        required
      ></v-text-field>
      <v-file-input
        v-if="isFile"
        :rules="imgRules"
        accept="image/png, image/jpeg, image/bmp"
        placeholder="Pick an avatar"
        prepend-icon="mdi-camera"
        label="Image File"
        @change="getImg"
      ></v-file-input>

      <v-switch class="ma-4" label="In Stock" v-model="toy.inStock"></v-switch>

      <v-btn :disabled="!valid" color="success" class="mr-4" @click="sendForm">Validate</v-btn>

      <v-btn color="error" class="mr-4" @click="reset">Reset Form</v-btn>

      <v-btn color="warning" @click="resetValidation">Reset Validation</v-btn>
    </v-form>
    {{toy}}
  </v-row>
</template>
<script>
export default {
  data: () => ({
    valid: true,
    isFile: true,
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 30) || "Name must be less than 30 characters"
    ],
    priceRules: [
      v => !!v || "Price is required",
      v => (v && Number.isInteger(+v)) || `Only digits`
    ],
    urlRules: [v => !!v || "Image is required"],
    imgRules: [
      value =>
        !value ||
        value.size < 5000000 ||
        "Avatar size should be less than 2 MB!"
    ],
    toy: {
      name: "",
      price: "",
      type: "",
      inStock: true,
      imgUrl: ""
    }
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    sendForm() {
      if (this.$refs.form.validate()) {
        if (this.toy._id)
          this.$store
            .dispatch({ type: "updateToy", toy: this.toy })
            .then(toy => this.$router.push(`/toy/details/${toy._id}`));
        else
          this.$store
            .dispatch({ type: "addToy", toy: this.toy })
            .then(toy => this.$router.push(`/toy/details/${toy._id}`));
      }
    },
    getImg(ev) {
      console.log(ev);

      // console.log(ev.target.files[0]);

      this.toy.imgUrl = ev;
    }
    // switchFile() {
    //   this.isFile = !this.isFile;
    // }
  },
  computed: {
    types() {
      const type = this.$store.getters.toysType;
      return Array.from(new Set(type));
    }
  },
  created() {
    const id = this.$route.params.id;
    if (id) {
      this.$store.dispatch({ type: "toyById", id }).then(toy => {
        this.toy = toy;
        this.isFile = false;
      });
    }
  }
};
</script>