<template>
  <div>
    <div class="search">
      <v-text-field v-model="filterBy.name" label="Search" @keyup="setFilter"></v-text-field>

      <v-select :items="items" label="Type" v-model="filterBy.type" @change="setFilter"></v-select>
    </div>
    <div class="sort">
      <span>Sort</span>
      <v-radio-group v-model="filterBy.sort" row>
        <v-radio label="Name" value="name"></v-radio>
        <v-radio label="Price" value="price"></v-radio>
      </v-radio-group>
      <span>State</span>
      <v-switch v-model="filterBy.stock" label="In Stock" @change="setFilter"></v-switch>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    toys: Array
  },
  data() {
    return {
      filterBy: {
        name: "",
        stock: false,
        type: "All",
        sort: "name"
      }
    };
  },
  methods: {
    setFilter() {
      this.$emit("filterd", (JSON.parse(JSON.stringify(this.filterBy))));
    }
  },
  computed: {
    items() {
      return this.$store.getters.toysType
    }
  }
};
</script>
<style scoped>
.sort {
  display: flex;
  justify-content: center;
}
.search {
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>