'use strict'
import httpService from './http-service.js'
import cloudinaryService from './cloudinary.service.js'

export default {
    query,
    getById,
    add,
    update,
    remove
}
const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? '/api/toy'
    : '//localhost:3150/api/toy';
function query() {
    return httpService.get(BASE_URL)
        .then(res => res.data)
}
function getById(id) {
    return httpService.get(`${BASE_URL}/${id}`)
        .then(res => res.data)
}
function add(toy) {
    return cloudinaryService.uploadImg(toy.imgUrl)
        .then(res => res.url)
        .then(url => {
            toy.imgUrl = url
            return httpService.post(BASE_URL, toy)
                .then(res => res.data)
        })
}
function update(toy) {
    console.log(toy);
    
    return httpService.put(`${BASE_URL}/${toy._id}`, toy)
        .then(res => res.data)
}
function remove(id) {
    return httpService.delete(`http://localhost:3000/toy/${id}`)
        .then(res => res.data)
}